ackground-color: transparent;\n  background-image: url(data:image/png;base64,), url(data:image/png;base64,);\n  -webkit-animation: snow 20s linear infinite;\n  animation: snow 20s linear infinite;\n  z-index: 999;\n  right: 0;\n  top: 0;\n  left: 0;\n  bottom: 0;\n  margin-top: 0;\n  pointer-events: none;\n  position: absolute;\n}\n/*Keyframes*/\n@keyframes snow {\n  0% {\n    background-position: 0px 0px, 0px 0px, 0px 0px\n  }\n  100% {\n    background-position: 500px 1000px, 400px 400px, 300px 300px\n  }\n}\n@-webkit-keyframes snow {\n  0% {\n    background-position: 0px 0px, 0px 0px, 0px 0px\n  }\n  100% {\n    background-position: 500px 1000px, 400px 400px, 300px 300px;\n  }\n}\n/* full loading */\n.loading-bk {\n  background-color: #faf8f2;\n}\n.loading {\n  position: absolute;\n  top: 50%;\n  left: 50%;\n}\n.loading-bar {\n  display: inline-block;\n  width: 4px;\n  height: 18px;\n  border-radius: 4px;\n  -webkit-animation: loading 1s ease-in-out infinite;\n          animation: loading 1s ease-in-out infinite;\n}\n.loading-bar:nth-child(1) {\n  background-color: #3498db;\n  -webkit-animation-delay: 0;\n          animation-delay: 0;\n}\n.loading-bar:nth-child(2) {\n  background-color: #c0392b;\n  -webkit-animation-delay: 0.09s;\n          animation-delay: 0.09s;\n}\n.loading-bar:nth-child(3) {\n  background-color: #f1c40f;\n  -webkit-animation-delay: .18s;\n          animation-delay: .18s;\n}\n.loading-bar:nth-child(4) {\n  background-color: #27ae60;\n  -webkit-animation-delay: .27s;\n          animation-delay: .27s;\n}\n@-webkit-keyframes loading {\n  0% {\n    -webkit-transform: scale(1);\n            transform: scale(1);\n  }\n  20% {\n    -webkit-transform: scale(1, 2.2);\n            transform: scale(1, 2.2);\n  }\n  40% {\n    -webkit-transform: scale(1);\n            transform: scale(1);\n  }\n}\n@keyframes loading {\n  0% {\n    -webkit-transform: scale(1);\n            transform: scale(1);\n  }\n  20% {\n    -webkit-transform: scale(1, 2.2);\n            transform: scale(1, 2.2);\n  }\n  40% {\n    -webkit-transform: scale(1);\n            transform: scale(1);\n  }\n}\n/* scroll */\n/* 设置滚动条的样式 */\n::-webkit-scrollbar {\n  width: 8px;\n}\n/* 滚动槽 */\n::-webkit-scrollbar-track {\n  /* -webkit-box-shadow: inset 0 0 6px rgba(24, 19, 19, 0.671); */\n  border-radius: 10px;\n}\n/* 滚动条滑块 */\n::-webkit-scrollbar-thumb {\n  border-radius: 10px;\n  background: #bbb;\n  -webkit-box-shadow: inset 0 0 6px rgba(14, 16, 20, 0.678);\n}\n::-webkit-scrollbar-thumb:window-inactive {\n  background: rgba(41, 39, 39, 0.699);\n}\n/* loading_submit */\n.lds-dual-ring {\n  display: inline-block;\n  width: 40px;\n  height: 40px;\n}\n.lds-dual-ring:after {\n  content: \" \";\n  display: block;\n  width: 20px;\n  height: 20px;\n  margin: 1px;\n  border-radius: 50%;\n  border: 2px solid black;\n  border-color: black transparent black transparent;\n  -webkit-animation: lds-dual-ring 1.2s linear infinite;\n          animation: lds-dual-ring 1.2s linear infinite;\n}\n@-webkit-keyframes lds-dual-ring {\n  0% {\n    -webkit-transform: rotate(0deg);\n            transform: rotate(0deg);\n  }\n  100% {\n    -webkit-transform: rotate(360deg);\n            transform: rotate(360deg);\n  }\n}\n@keyframes lds-dual-ring {\n  0% {\n    -webkit-transform: rotate(0deg);\n            transform: rotate(0deg);\n  }\n  100% {\n    -webkit-transform: rotate(360deg);\n            transform: rotate(360deg);\n  }\n}\n"